export const query = graphql`
  query storeByCategoryAndLocation(
    $categoryName: String
    $locationName: String
  ) {
    allStrapiStores(
      filter: {
        categories: { elemMatch: { slug: { eq: $categoryName } } }
        locations: { elemMatch: { slug: { eq: $locationName } } }
      }
    ) {
      nodes {
        id
        slug
        name
        nameTa
        aboutStore
        aboutStoreTa
        categories {
          id
          label
          labelTa
          slug
        }
        locations {
          id
          label
          labelTa
          slug
        }
        gallery {
          desc
          id
          images {
            id
            image {
              childImageSharp {
                fixed(width: 200, quality: 100) {
                  ...GatsbyImageSharpFixed_withWebp
                }
              }
            }
          }
        }
      }
    }
  }
`;

function getCategoryServiceSchema(store) {
  console.log(store);

  const addressSchema = {
    address: {
      "@type": "PostalAddress",
      addressLocality: store.locationName,
      addressRegion: store.locationName,
    },
  };
  const serviceSchema = {
    "@context": "http://schema.org/",
    "@type": "Service",
    serviceType: store.categoryItem.label.includes("wedding")
      ? store.categoryItem.label
      : `Wedding ${store.categoryItem.label}`,
    areaServed: {
      "@type": "Place",
      ...addressSchema,
    },
  };

  return [serviceSchema];
}

export default injectIntl(CategoryListWithLocation);
